.about {
  background-color: #0b0e13;
  padding: 140px 24px;
  color: #ffffff;
  width: 100%;
}

.about-container {
  margin: 0 auto;
  text-align: center;
  max-width: 100%;
}

.about-title {
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 32px;
}

.about-description {
  max-width: 800px;
  margin: 0 auto 80px;
  font-size: 16px;
  line-height: 1.8;
  color: #b5bcc7;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 32px;
  text-align: left;
}

/* ðŸ“Œ Add responsiveness for Next.js */
@media (max-width: 1024px) {
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 640px) {
  .stats-grid {
    grid-template-columns: 1fr;
  }
}

.stat-card {
  padding: 32px 24px;
  border: 1px solid #1f2430;
}

.stat-card--highlight {
  background: linear-gradient(135deg, #6aa9ff, #9b6cff, #f4d06f);
  color: #ffffff;
}

.stat-title {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 12px;
}

.stat-description {
  font-size: 14px;
  color: #b5bcc7;
}

.stat-card--highlight .stat-description {
  color: #ffffff;
}